export default "<template>\n  <div>\n    <h1>Tabs</h1>\n    <p>Used for Tabs.</p>\n\n    <pre><code>import { Tabs } from 'streamlabs-beaker';\n\ncomponents: {\n  Tabs\n}</code></pre>\n\n    <div class=\"section\">\n      <DemoSection title=\"Default\" :code=\"demoCode\">\n        <template #components>\n          <Tabs\n            :tabs=\"tabs\"\n            size=\"small\"\n            :update-route=\"false\"\n            selected=\"advanced\"\n          >\n            <div :slot=\"tab.value\" v-for=\"tab in tabs\" :key=\"tab.value\">\n              {{ tab.name }}\n            </div>\n          </Tabs>\n        </template>\n      </DemoSection>\n    </div>\n\n    <div class=\"section\">\n      <DemoSection title=\"New Tabs\" :code=\"demoCode\">\n        <template #components>\n          <TabsNew :tabs=\"tabs\" size=\"small\" selected=\"advanced\">\n            <div :slot=\"tab.value\" v-for=\"tab in tabs\" :key=\"tab.value\">\n              {{ tab.name }}\n            </div>\n          </TabsNew>\n        </template>\n      </DemoSection>\n    </div>\n\n    <table class=\"docs-table\">\n      <thead>\n        <tr>\n          <th>Prop</th>\n          <th>Type</th>\n          <th>Default</th>\n          <th>Description</th>\n        </tr>\n      </thead>\n      <tbody>\n        <tr>\n          <td>tabs</td>\n          <td>array</td>\n          <td>null</td>\n          <td>\n            tabs information to display. you can make array like below\n            <br />\n            <code\n              >tabs = [ { name: \"General\", value: \"general\", icon: \"information\"\n              }, ... ];</code\n            >\n          </td>\n        </tr>\n        <tr>\n          <td>size</td>\n          <td>string</td>\n          <td>small</td>\n          <td>text size of tabs. Options are \"small\" and \"large\"</td>\n        </tr>\n        <tr>\n          <td>selected</td>\n          <td>string</td>\n          <td>null</td>\n          <td>default selected tab</td>\n        </tr>\n        <tr>\n          <td>updateRoute</td>\n          <td>boolean</td>\n          <td>true</td>\n          <td>\n            Updates current URL with \"#/{selected Tab}\". Set to false if you\n            don't want to update the URL.\n          </td>\n        </tr>\n      </tbody>\n    </table>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport { Component, Vue } from \"vue-property-decorator\";\n\nimport DemoSection from \"./../components/DemoSection.vue\";\nimport ScrollNav from \"./../components/ScrollNav.vue\";\nimport Tabs from \"./../components/Tabs.vue\";\nimport TabsNew from \"./../components/TabsNew.vue\";\nimport TabsCode from \"./Tabs.vue?raw\";\n\n@Component({\n  components: {\n    DemoSection,\n    ScrollNav,\n    Tabs,\n    TabsNew\n  }\n})\nexport default class TabsDemo extends Vue {\n  demoCode = TabsCode;\n  tabs = [\n    {\n      name: \"General\",\n      value: \"general\",\n      icon: \"information\"\n    },\n    {\n      name: \"Advanced\",\n      value: \"advanced\",\n      icon: \"\"\n    },\n    {\n      name: \"Account\",\n      value: \"account\",\n      icon: \"\"\n    },\n    {\n      name: \"Integrations\",\n      value: \"integrations\",\n      icon: \"\"\n    },\n    {\n      name: \"Payments\",\n      value: \"payments\",\n      icon: \"\"\n    },\n    {\n      name: \"Donations\",\n      value: \"donations\",\n      icon: \"\"\n    },\n    {\n      name: \"Subscriptions\",\n      value: \"subscriptions\",\n      icon: \"\"\n    },\n    {\n      name: \"Preferences\",\n      value: \"preferences\",\n      icon: \"\"\n    },\n    {\n      name: \"Apps\",\n      value: \"apps\",\n      icon: \"\"\n    },\n    {\n      name: \"Merch\",\n      value: \"merch\",\n      icon: \"\"\n    },\n    {\n      name: \"API\",\n      value: \"api\",\n      icon: \"\"\n    },\n    {\n      name: \"Moderators\",\n      value: \"moderators\",\n      icon: \"\"\n    },\n    {\n      name: \"Themes\",\n      value: \"themes\",\n      icon: \"\"\n    }\n  ];\n}\n</script>\n"