
<div class="s-tabs-wrapper">
  <div class="s-tabs-nav-wrapper">
    <div class="s-tabs-nav" :class="className">
      <div
        v-if="hasPrev"
        @click="scrollLeft"
        class="s-tabs-nav__control s-has-prev"
      >
        <i class="icon-back"></i>
      </div>

      <div
        ref="scrollable_tabs"
        @scroll="calculateScrolls"
        class="s-tabs"
        :class="{
          's-has-next': hasNext,
          's-has-prev': hasPrev
        }"
      >
        <div
          v-for="tab in tabs"
          :key="tab.value"
          class="s-tab"
          :class="{ 'is-active': tab.value === selectedTab }"
          :style="selectTabSize"
          @click="showTab(tab.value)"
        >
          <router-link
            v-if="updateRoute"
            :to="`#/${tab.value}`"
            class="s-tab-link"
          >
            <i v-if="tab.icon" :class="`icon-${tab.icon}`"></i>
            {{ tab.name }}
          </router-link>
          <div v-else class="s-tab-link">
            <i v-if="tab.icon" :class="`icon-${tab.icon}`"></i>
            {{ tab.name }}
          </div>
        </div>
      </div>

      <div
        v-if="hasNext"
        @click="scrollRight"
        class="s-tabs-nav__control s-has-next"
      >
        <i class="icon-back"></i>
      </div>
    </div>
  </div>

  <div class="s-tab-content" v-if="!hideContent">
    <div
      v-for="(tab, index) in tabs"
      :key="index"
      v-show="tab.value === selectedTab"
    >
      <slot :name="tab.value" />
    </div>
  </div>
</div>
